<template>
  <v-card class="mx-auto my-3" outlined width="256">
    <v-list-item three-line>
      <v-list-item-content>
        <v-list-item-title class="text-h5 mb-1">
          {{ item.name }}
        </v-list-item-title>
        <v-list-item-subtitle>{{ item.description }}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <div class="pa-2">
      <v-chip v-if="item.language" class="ma-2" small>
        {{ item.language }}
      </v-chip>

      <div class="px-2">
        <small>{{ formatDate(item.created_at) }}</small>
      </div>
    </div>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        v-if="item.url"
        text
        small
        icon
        :href="item.html_url"
        target="_blank"
        ><v-icon>mdi-github</v-icon></v-btn
      >
      <v-btn text small icon><v-icon>mdi-eye</v-icon></v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  methods: {
    formatDate: (created_at) =>
      new Date(created_at).toISOString().split("T")[0],
  },
};
</script>

