<template>
  <v-app>
    <v-container>
      <v-card class="d-flex">
        <aside-component></aside-component>
        <content-component :prjList="prjList"></content-component>
      </v-card>
      <v-footer padless>
        <v-col class="text-center" cols="12">
          {{ new Date().getFullYear() }} — <strong>@zzharuk</strong>
        </v-col>
      </v-footer>
    </v-container>
  </v-app>
</template>

<script>
import AsideComponent from "./AsideComponent.vue";
import ContentComponent from "./ContentComponent.vue";
export default {
  data: () => ({
    prjList: [],
  }),
  components: {
    AsideComponent,
    ContentComponent,
  },
  mounted() {
    this.$axios
      .get("https://api.github.com/users/zzharuk/repos")
      .then((response) => {
        this.prjList = response.data.sort((repo_a, repo_b) => {
          return new Date(repo_b.created_at) - new Date(repo_a.created_at);
        });
      });
  },
};
</script>

