<template>
  <v-container>
    <h1>{{ title }}</h1>
    <v-divider></v-divider>
    <v-row>
      <v-col cols="12" sm="6" md="3" class="mt-3 ml-auto">
        <v-text-field
          label="Search project"
          solo
          v-model="search"
        ></v-text-field>
      </v-col>
    </v-row>
    <div class="projects-wrapper row mt-5">
      <v-col col="12" sm="6" md="4" v-for="prj in filteredList" :key="prj.id">
        <ProjectItemComponent :item="prj" />
      </v-col>
    </div>
  </v-container>
</template>

<script>
import ProjectItemComponent from "./ProjectItemComponent.vue";

export default {
  components: {
    ProjectItemComponent,
  },
  props: {
    prjList: {
      type: [Object, Array],
      required: true,
    },
  },
  data: () => ({
    title: "Projects v0.2",
    search: "",
  }),
  computed: {
    filteredList() {
      return Object.values(this.prjList).filter((item) => {
        let searchCond =
          !this.search ||
          item.name.toLowerCase().indexOf(this.search.toLowerCase()) > -1;
        return searchCond;
      });
    },
  },
};
</script>

